<template>
  <v-modal
    ref="modal"
    v-model="isVisible"
    size="w-[40rem]"
  >
    <template #header> Upload supporting document </template>

    <v-info class="mb-2">You can pick any suggested name of the document or enter it manually</v-info>

    <form class="flex flex-col gap-5">
      <v-label label="Document name">
        <v-dropdown
          v-model="form.name"
          :options="nameOptions"
          show-clear
          editable
          class="w-full"
        />
      </v-label>

      <v-file
        v-model="form.pdf"
        accept=".pdf"
        show-file-name
        :loading="isUploading"
        :progress="uploadingProgress"
      >
        Choose document in PDF format or drag it here
      </v-file>
    </form>

    <template #footer>
      <v-button
        variant="primary"
        :loading="isUploading"
        @click="upload"
      >
        Upload
      </v-button>
    </template>
  </v-modal>
</template>

<script setup lang="ts">
import { VModal } from "#components"
import type { Payload } from "core/types/api"

const props = defineProps<{
  legalEntityId: number
}>()

const emit = defineEmits<{
  (e: "uploaded"): void
}>()

const modal = ref<InstanceType<typeof VModal>>()
const isVisible = ref(false)

defineExpose({
  show: () => modal.value?.show(),
  hide: () => modal.value?.hide(),
})

const form = reactive<Payload<typeof addLegalFile>>({
  legal_entity_id: props.legalEntityId,
  name: "",
  pdf: undefined,
})

const nameOptions = [
  "Extract from corporate registry",
  "VAT certificate",
  "Articles of association",
  "Power of attorney",
  "Proof of address",
  "Bank statement",
].map((name) => ({ label: name, value: name }))

const isUploading = ref(false)
const uploadingProgress = ref(0)
async function upload() {
  if (isUploading.value) {
    return
  }

  isUploading.value = true
  try {
    // TODO cancel request when closing modal
    await addLegalFile(form, {
      onUploadProgress: (progressEvent) => {
        uploadingProgress.value = calculateUploadProgress(progressEvent)
      },
    })
    emit("uploaded")
    isVisible.value = false
    form.name = ""
    form.pdf = undefined
  } finally {
    uploadingProgress.value = 0
    isUploading.value = false
  }
}
</script>
