<template>
  <div>
    <v-h1>Legal overview</v-h1>

    <template v-if="entity">
      <div class="flex items-center gap-4">
        <v-h2>
          {{ entity.name }}
        </v-h2>
        <v-button
          variant="primary"
          to="/legal/info/manage"
        >
          Edit information
        </v-button>
      </div>

      <div class="mt-4 flex flex-col items-stretch gap-5">
        <div class="flex gap-5">
          <v-label label="Status">
            <v-html :content="entity.status_translated" />
          </v-label>

          <v-label
            v-if="entity.last_status_validation_issue"
            label="Last status validation issue"
          >
            <v-html
              class="whitespace-pre-line"
              :content="entity.last_status_validation_issue"
            />
          </v-label>
        </div>

        <div class="flex flex-wrap gap-5">
          <v-label label="Legal type">
            {{ entity.type }}
          </v-label>
          <v-label label="Legal name">
            {{ entity.name }}
          </v-label>
          <v-label label="Legal address">
            {{ entity.address }}
          </v-label>
          <v-label label="Country">
            {{ entity.country }}
          </v-label>
          <v-label label="VAT number">
            {{ entity.vat_number }}
          </v-label>
          <v-label label="Registry code">
            {{ entity.registry_code }}
          </v-label>
        </div>

        <div>
          <v-h3 class="mb-2">Representative</v-h3>
          <div class="flex gap-5">
            <v-label label="Name">
              {{ entity.representative_name }}
            </v-label>
            <v-label label="Email">
              {{ entity.representative_email }}
            </v-label>
            <v-label label="Phone">
              {{ entity.representative_number }}
            </v-label>
            <v-label label="Position">
              {{ entity.representative_position }}
            </v-label>
          </div>
        </div>

        <div>
          <v-h3 class="mb-2">Signatory</v-h3>
          <div class="flex gap-5">
            <v-label label="Name">
              {{ entity.signatory_name }}
            </v-label>
            <v-label label="Email">
              {{ entity.signatory_email }}
            </v-label>
            <v-label label="Phone">
              {{ entity.signatory_number }}
            </v-label>
            <v-label label="Position">
              {{ entity.signatory_position }}
            </v-label>
          </div>
        </div>

        <div>
          <v-h3 class="mb-2">Bank account details</v-h3>
          <div class="flex flex-wrap gap-5">
            <v-label label="Bank name">
              {{ entity.bank_name }}
            </v-label>
            <v-label label="Address">
              {{ entity.bank_address }}
            </v-label>
            <v-label label="BIC/SWIFT">
              {{ entity.bank_swift }}
            </v-label>
            <v-label label="Account number / IBAN">
              {{ entity.bank_iban }}
            </v-label>
          </div>
        </div>
      </div>
    </template>
    <v-button
      v-else
      variant="primary"
      to="/legal/info/manage"
    >
      Add information
    </v-button>

    <div v-if="entity">
      <div class="mb-2 mt-5 flex items-center gap-4">
        <v-modal-upload-document
          ref="uploadDocumentModal"
          :legal-entity-id="entity.id"
          @uploaded="loadDocuments"
        />

        <v-h2>Supporting documents</v-h2>
        <v-button
          variant="primary"
          @click="uploadDocumentModal?.show()"
        >
          Upload
        </v-button>
      </div>

      <v-table :loading="areDocumentsLoading">
        <template #head>
          <v-tr>
            <v-th>Name</v-th>
            <v-th>Status</v-th>
            <v-th>Last status validation issue</v-th>
            <v-th></v-th>
          </v-tr>
        </template>

        <template #empty> No documents found </template>

        <template #body>
          <v-tr
            v-for="document of documents"
            :key="document.id"
          >
            <v-td>{{ document.name }}</v-td>
            <v-td>{{ document.status_translated }}</v-td>
            <v-td>{{ document.last_status_validation_issue }}</v-td>
            <v-td>
              <div class="flex justify-end gap-1.5">
                <v-button
                  v-if="document.status !== LegalFileStatus.Ok"
                  :loading="documentBeingDeleted === document.id"
                  variant="danger"
                  @click="deleteDocument(document.id)"
                >
                  Delete
                </v-button>
              </div>
            </v-td>
          </v-tr>
        </template>
      </v-table>
    </div>

    <v-h2 class="mb-2 mt-5">Agreements</v-h2>
    <v-table>
      <template #head>
        <v-tr>
          <v-th>Document title</v-th>
          <v-th>Signed</v-th>
          <v-th>Signatory</v-th>
          <v-th>Signing date</v-th>
          <v-th></v-th>
        </v-tr>
      </template>

      <template #empty> No agreements found </template>

      <template #body>
        <v-tr
          v-for="agreement of agreements"
          :key="agreement.id"
        >
          <v-td>{{ agreement.name }}</v-td>
          <v-td>{{ agreement.signed_at ? "Yes" : "No" }}</v-td>
          <v-td>{{ agreement.signatory_name }}</v-td>
          <v-td>{{ agreement.signed_at ? new Date((agreement.signed_at as number) * 1000).toLocaleString() : "" }}</v-td>
          <v-td>
            <div class="flex justify-end gap-1.5">
              <v-button
                :to="agreement.uri"
                target="_blank"
              >
                View / Download
              </v-button>
            </div>
          </v-td>
        </v-tr>
      </template>
    </v-table>
  </div>
</template>

<script setup lang="ts">
import { VModalUploadDocument } from "#components"
import { LegalFileStatus, type LegalFile } from "core/types/api"

const entity = await getLegalEntity().catch((e) => {
  if (e instanceof APIError && e.hasCode("ERROR_LEGAL_ENTITY_NOT_DEFINED")) {
    return null
  }

  throw e
})

const uploadDocumentModal = ref<InstanceType<typeof VModalUploadDocument>>()
const documents = ref<LegalFile[]>([])
const areDocumentsLoading = ref(false)
await loadDocuments()
async function loadDocuments() {
  if (!entity) {
    return []
  }

  areDocumentsLoading.value = true

  try {
    const response = await getLegalFiles({ legal_entity_id: entity.id })
    documents.value = response
  } catch {
    documents.value = []
  } finally {
    areDocumentsLoading.value = false
  }
}

const documentBeingDeleted = ref<null | number>()
async function deleteDocument(id: number) {
  if (documentBeingDeleted.value || !confirm("Are you sure you want to delete this document?")) {
    return
  }

  documentBeingDeleted.value = id
  try {
    await deleteLegalFile({ id })

    const idx = documents.value.findIndex((doc) => doc.id === id)
    documents.value.splice(idx, 1)
  } finally {
    documentBeingDeleted.value = null
  }
}

const { allAgreements: agreements } = storeToRefs(useAgreementsStore())
</script>
